import { useCallback, useEffect, useReducer, useRef } from 'react';
import { forEach, isEqual, difference, isNull, omit } from 'lodash';
import type { DataSet } from '@splunk/datasource-utils';
import usePrevious from './usePrevious';

interface DataSourceError {
    level: 'error';
    message: string;
}

export type Meta = Record<string, unknown>;
export type RequestParams = Record<string, unknown>;

type BindingRemovePayload = string;
type BindingResetPayload = string;
interface BindingErrorPayload {
    binding: string;
    error: DataSourceError;
}
interface BindingUpdatePayload {
    binding: string;
    data: DataSet;
    meta: Meta;
    requestParams: RequestParams;
}

type Action =
    | { type: 'binding/remove'; payload: BindingRemovePayload }
    | { type: 'binding/reset'; payload: BindingResetPayload }
    | {
          type: 'binding/error';
          payload: BindingErrorPayload;
      }
    | {
          type: 'binding/update';
          payload: BindingUpdatePayload;
      };

interface StateItem {
    error: DataSourceError | null;
    data: DataSet | null;
    meta: Meta | null;
    requestParams: RequestParams | null;
}

type BindingType = string;
type State = Record<BindingType, StateItem>;

interface DataSourceDefinition {
    type: string;
    name?: string;
    options?: Record<string, unknown>;
}

export interface Binding {
    bindingType: string;
    dataSourceId: string;
    dataSource: DataSourceDefinition;
    initialRequestParams: RequestParams;
}

interface AllocateArgs {
    dataSourceId: string;
}

interface SubscribeArgs {
    consumerId: string;
    initialRequestParams: RequestParams;
}

type Refresh = () => void;

interface DataSourceController {
    subscribe: ({
        consumerId,
        initialRequestParams,
    }: SubscribeArgs) => Promise<DataSourceSubscription>;
    refresh: Refresh;
}

type HandleDataPayload = Omit<BindingUpdatePayload, 'binding'>;

interface SubscribeToDataArgs {
    onData: (payload: HandleDataPayload) => void;
    onError: (error: DataSourceError) => void;
}

interface DataSourceSubscription {
    subscribeToData: ({ onData, onError }: SubscribeToDataArgs) => void;
    cancel: () => void;
    updateRequestParams: (requestParams: RequestParams) => void;
    dataSource: DataSourceController;
    consumerId: string;
}

export interface DataSourceRegistry {
    allocate: ({ dataSourceId }: AllocateArgs) => DataSourceController;
}

type Consumers = Record<string, DataSourceSubscription>;
type Controllers = Record<string, DataSourceController>;

export type OnUpdate = (
    payload: BindingUpdatePayload | BindingErrorPayload
) => void;

interface UseDataSourcesArgs {
    consumerId: string;
    bindings: Binding[];
    dataSourceRegistry: DataSourceRegistry;
    onUpdate?: OnUpdate;
}

interface UseDataSourcesReturn {
    loading: boolean;
    dataSources: State;
    updateRequestParams: (
        binding: string,
        newRequestParams: RequestParams
    ) => void;
    refresh: Refresh;
    getSubscribers: () => Consumers;
}

const reducer = (state: State, action: Action) => {
    switch (action.type) {
        case 'binding/remove': {
            const binding = action.payload;

            return omit(state, [binding]);
        }
        case 'binding/reset': {
            const binding = action.payload;

            return {
                ...state,
                [binding]: {
                    error: null,
                    data: null,
                    meta: null,
                    requestParams: null,
                },
            };
        }
        case 'binding/error': {
            const { binding, error } = action.payload;

            return {
                ...state,
                [binding]: {
                    error,
                    data: null,
                    meta: null,
                    requestParams: null,
                },
            };
        }
        case 'binding/update': {
            const { binding, data, meta, requestParams } = action.payload;
            return {
                ...state,
                [binding]: { error: null, data, meta, requestParams },
            };
        }
        default:
            return state ?? {};
    }
};

const useDataSources = ({
    consumerId,
    bindings,
    dataSourceRegistry,
    onUpdate,
}: UseDataSourcesArgs): UseDataSourcesReturn => {
    const prevProps = usePrevious({
        consumerId,
        bindings,
        dataSourceRegistry,
    });
    const consumers = useRef<Consumers>({});
    const controllers = useRef<Controllers>({});
    const [results, dispatch] = useReducer(reducer, {});

    const handleError = useCallback(
        (binding) => (error: DataSourceError) => {
            dispatch({ type: 'binding/error', payload: { binding, error } });
            if (onUpdate) {
                onUpdate({
                    binding,
                    error,
                });
            }
        },
        [onUpdate]
    );

    const handleData = useCallback(
        (binding: string) => (response: HandleDataPayload) => {
            const payload = { binding, ...response };
            dispatch({
                type: 'binding/update',
                payload,
            });
            if (onUpdate) {
                onUpdate(payload);
            }
        },
        [onUpdate]
    );

    const cancelSubscription = useCallback((binding: string) => {
        if (consumers.current[binding]) {
            consumers.current[binding].cancel();
            delete consumers.current[binding];
            delete controllers.current[binding];
            dispatch({ type: 'binding/remove', payload: binding });
        }
    }, []);

    const updateRequestParams = useCallback((binding, newRequestParams) => {
        if (consumers.current[binding]) {
            consumers.current[binding].updateRequestParams(newRequestParams);
        }
    }, []);

    const refresh = useCallback(() => {
        forEach(controllers.current, (controller, binding) => {
            dispatch({ type: 'binding/reset', payload: binding });
            controller.refresh();
        });
    }, []);

    const createSubscription = useCallback(
        async ({ bindingType, dataSourceId, initialRequestParams }) => {
            try {
                const controller = dataSourceRegistry.allocate({
                    dataSourceId,
                });
                const sub = await controller.subscribe({
                    consumerId,
                    initialRequestParams,
                });
                sub.subscribeToData({
                    onData: handleData(bindingType),
                    onError: handleError(bindingType),
                });
                consumers.current[bindingType] = sub;
                controllers.current[bindingType] = controller;
            } catch (err) {
                handleError(bindingType)({
                    level: 'error',
                    message: err.message,
                });
            }
        },
        [consumerId, dataSourceRegistry, handleData, handleError]
    );

    useEffect(() => {
        // Prune out consumers that are no longer in the binding list
        const unneededConsumers = difference(
            Object.keys(consumers.current),
            bindings.map((b) => b.bindingType)
        );
        unneededConsumers.forEach(cancelSubscription);

        // For everything left,
        // determine if it is a new subscription to create, or has had a definition update
        forEach(bindings, (binding) => {
            const { bindingType, dataSource, dataSourceId } = binding;
            const prevBinding = prevProps?.bindings.find(
                (b) => b.bindingType === bindingType
            );
            if (!prevBinding) {
                // this is async, but we probably don't need to await because subscriptions should be independent
                createSubscription(binding);
            } else if (
                !isEqual(prevBinding.dataSource, dataSource) ||
                prevBinding.dataSourceId !== dataSourceId ||
                consumerId !== prevProps?.consumerId
            ) {
                dispatch({ type: 'binding/reset', payload: bindingType });
                cancelSubscription(bindingType);
                createSubscription(binding);
            }
        });
    }, [
        bindings,
        createSubscription,
        cancelSubscription,
        prevProps?.bindings,
        prevProps?.consumerId,
        consumerId,
    ]);

    useEffect(() => {
        const currentConsumers = consumers.current;
        return () => {
            forEach(currentConsumers, (sub) => sub.cancel());
        };
    }, []);

    return {
        loading: results.primary
            ? isNull(results.primary.error) && isNull(results.primary.data)
            : false,
        dataSources: results,
        updateRequestParams,
        refresh,
        getSubscribers() {
            return consumers.current;
        },
    };
};

export default useDataSources;
