import React, {
    ReactNode,
    useMemo,
    useContext,
    useRef,
    useCallback,
    MutableRefObject,
} from 'react';
import DashboardApiContext from '@splunk/dashboard-context/DashboardApiContext';
import DataSourceContext from '@splunk/visualization-context/DataSourceContext';
import type { DashboardApi } from '@splunk/dashboard-types';
import useDataSources, {
    Binding,
    DataSourceRegistry,
    OnUpdate,
} from './hooks/useDataSources';

export interface DataSourceApi {
    subscribe: (bindingType: string, callback: OnUpdate) => () => void;
}

interface Props {
    consumerId: string;
    bindings: Binding[];
    children: ReactNode;
}

export type Subscribers = Record<string, OnUpdate[]>;

// a helper function to help testing the subscribe and unsubscribe
export const createSubscribe = (subscribers: MutableRefObject<Subscribers>) => (
    bindingType: string,
    callback: OnUpdate
): (() => void) => {
    if (!subscribers.current[bindingType]) {
        // eslint-disable-next-line no-param-reassign
        subscribers.current[bindingType] = [];
    }
    subscribers.current[bindingType].push(callback);

    return () => {
        const idx = subscribers.current[bindingType].indexOf(callback);

        if (idx === -1) {
            return;
        }

        subscribers.current[bindingType].splice(idx, 1);
    };
};

export const DataSourceContextProvider = ({
    consumerId,
    bindings,
    children,
}: Props): JSX.Element => {
    const dashboardApi = useContext<DashboardApi | null>(
        DashboardApiContext
    ) as DashboardApi;
    const dataSourceRegistry = dashboardApi.getDataSourceRegistry() as DataSourceRegistry;
    const subscribers = useRef<Subscribers>({});

    const onUpdate = useCallback<OnUpdate>(({ binding, ...rest }) => {
        const subs = subscribers.current[binding] ?? [];

        subs.forEach((cb) => cb({ binding, ...rest }));
    }, []);

    useDataSources({
        consumerId,
        bindings,
        dataSourceRegistry,
        onUpdate,
    });

    const dataSourceApi = useMemo(() => {
        return {
            subscribe: createSubscribe(subscribers),
        };
    }, []);

    return (
        <DataSourceContext.Provider value={dataSourceApi}>
            {children}
        </DataSourceContext.Provider>
    );
};

export { default as DataSourceContext } from '@splunk/visualization-context/DataSourceContext';
